.blackCard_info {
    color: white;
}

.blackCard_info > h3 {
    font-weight: 300;
}

.blackCard_info > h2 {
    margin-top: 15px;
}

.blackCard {
    margin: 40px 20px;
}

.blackCard > img {
    border-radius: 8px;
    object-fit: cover;
    height: 60vh;
    max-width: 25vw;
}

.blackCard > img:hover {
    cursor: pointer;
    transform: scale(1.05);
    transition: all 1s;
}
