.banner {
    background: url(../landscape-5811471_1920.jpg) center center;
    height: 87vh;
    position: relative;
}

.banner_info Button {
    color: rgb(51, 51, 51);
    border-radius: 12px;
    font-weight: 600;
}

.banner_info Button:hover {
    background-color: #ff7779;
    color: white;
}

.banner_info {
    color: white;
    position: absolute;
    bottom: 50px;
    margin-left: 40px;
    text-align: left;
}

.banner_info h1 {
    font-size: 8vw;
    line-height: 0.8;
}

.banner_info h5 {
    text-align: end;
    font-size: 1.7vw;
    max-width: 1000px;
    margin-top: 20px;
    margin-bottom: 20px;
}

#banner_searchButton {
    background-color: #ff7779af;
    font-weight: 900;
    text-transform: inherit;
    color: rgb(219, 206, 206);
    width: 30%;
    margin: auto;
    margin-top: 30px;
    border-radius: 999px;
    height: 45px;
    font-size: 1.25vw;
}

#banner_searchButton:hover {
    background-color: #ff7779;
    color: white;
}

.banner_search {
    display: flex;
    flex-direction: column;
}
