.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header_icon {
    object-fit: contain;
    height: 100px;
    margin-left: 40px;
}

.header_center {
    display: flex;
    margin-left: 8%;
    flex: 1;
    align-items: center;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border: 1px solid lightgray;
    border-radius: 999px;
}

.header_center > input {
    border: none;
    width: 250px;
    outline-style: none;
}

.header_right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 15vw;
    margin-right: 80px;
}

.header_right p {
    font-weight: 600;
}

.header_menu {
    display: none;
}

@media screen and (max-width: 768px) {
    .header_icon {
        width: 90px;
    }

    .header_right {
        display: none;
    }

    .header_center {
        margin-right: 40px;
    }

    .header_center > input {
        width: 40vw;
    }

    .header_menu {
        display: block;
        width: 50px;
        margin-right: 30px;
        top: 40px;
    }
}
