.search {
    margin: auto;
    z-index: 1;
}

.search h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
}

.search > input {
    border: none;
    position: absolute;
    background-color: white;
    width: 558px;
    height: 20px;
}

.search > input:focus {
    outline: none;
    font-weight: 500;
}
.search > .btn {
    position: absolute;
    color: white;
    background-color: #ff7779;
    top: 508px;
    width: 558px;
    text-transform: inherit;
}

.search > .btn:hover {
    background-color: #ff7779;
    color: white;
    font-size: 16px;
}
