.searchResult {
    display: flex;
    position: relative;
    margin: 20px;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    opacity: 1;
    cursor: pointer;
}

.searchResult:hover {
    opacity: 0.8;
}

.searchResult > img {
    height: 200px;
    width: 350px;
    border-radius: 10px;
    overflow: hidden;
    object-fit: cover;
}

.searchResult_info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
}

.searchResult_heart {
    position: absolute;
    top: 20px;
    right: 35px;
}

.searchResult_infoTop {
    width: 40vw;
}

.searchResult_infoTop > h3 {
    font-weight: 500;
    margin-top: 10px;
}

.searchResult_infoTop > p {
    margin-top: 10px;
    font-size: 14px;
    color: gray;
}

.searchResult_infoBottom {
    display: flex;
    justify-content: space-between;
}

.searchResult_star {
    color: rgb(240, 25, 25);
}

.searchResult_stars {
    display: flex;
    align-items: center;
}
